import React from 'react'
import data from '../../../data.json'

function Que1({que, ans}) {
    
    return (
        <div >
            <h5 className="subTitle">{que.des2}</h5>
            {que.q.map(res => 
                <div className="resultToPrint">
                    <p className="numb">{res.A} </p> 
                    <p className="questions">{res.d} </p> 
                    <div className="answers">
                        {Object.keys(ans).map( key => key === res.A ? 
                        que.des4.map(des => <h5 > {ans[key] === des ? <span>{des}</span> : des }</h5>) :''
                        )}
                    </div>
                </div>)}
        </div>
    )
}

export default Que1
