import React from 'react'

function Que6({que, ans}) {
    return (
        <div>
            <h5 className="subTitle">{que.des2}</h5>
            {que.q.map(res => 
                <div className="resultToPrint">
                    <p className="numb">{res.A} </p> 
                    <p className="questions">{res.d} </p> 
                    <div className="answers" style={{width: "100%"}}>
                        {Object.keys(ans).map( key => key === res.A ? 
                        que.des4.map(des => <h5 style={{width: "18%"}}> {ans[key] === des ? <span>{des}</span> : des }</h5>) :''
                        )}
                    </div>
                </div>)}
            
        </div>
    )
}

export default Que6
